export default {"updates":[{"date":"9/12/22","location":["-92.071","46.863"],"text":"I'm hiking the Superior Hiking Trail! Check back daily for trail updates!","mileage":"0","miles_to_date":"0"},{"date":"9/13/22","location":["-92.067","46.931"],"text":"Planes, buses, and trails, oh my! Finally on trail and spending my first night at White Pine with some new trail companions.","mileage":"6","miles_to_date":"6"},{"date":"9/14/22","location":["-91.913","47.033"],"text":"Met more folks on the trail, and happy to be kicking my feet up at Fox Farm. After my first 16 mile day, my feet hurt!","mileage":"16","miles_to_date":"22"},{"date":"9/15/22","location":["-91.784","47.111"],"text":"Rain, rain, go away ... dreaming of a hot shower and a cold drink in town tomorrow!","mileage":"14.3","miles_to_date":"36.3"},{"date":"9/16/22","location":["-91.682","47.026"],"text":"Town!! A bed! After 15 damp and grueling miles today, feeling grateful for simple comforts.","mileage":"15.3","miles_to_date":"51.6"},{"date":"9/18/22","location":["-91.420","47.199"],"text":"So many waterfalls! Plus a pupper at camp tonight! Things are looking up.","mileage":"15.5","miles_to_date":"67.1"},{"date":"9/19/22","location":["-91.301","47.258"],"text":"Rocky rollercoaster up and down, but beautiful ridge line views made it worth it!","mileage":"13.9","miles_to_date":"81"}]}